import React, { useEffect, useState } from "react";
import axios from "axios";
import {
  Container,
  Grid,
  Card,
  CardMedia,
  CardContent,
  Typography,
  CircularProgress,
  Box,
  Tooltip,
  Snackbar,
  Alert,
} from "@mui/material";
import { ThumbUp } from "@mui/icons-material";
import "./App.css";

const App = () => {
  const [animeData, setAnimeData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");

  useEffect(() => {
    const fetchAnimeData = async () => {
      try {
        const response = await axios.get("/anime", {
          params: { order: "votes" },
        });
        setAnimeData(response.data);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching anime data:", error);
        setLoading(false);
      }
    };

    fetchAnimeData();
  }, []);

  const handleCardClick = async (anime) => {
    if (anime.votes >= 100) {
      setSnackbarMessage(
        `Anime '${anime.name}' has reached the maximum vote count.`
      );
      setSnackbarOpen(true);
      return;
    }

    const updatedAnimeData = animeData.map((a) =>
      a.id === anime.id ? { ...a, votes: a.votes + 1 } : a
    );
    setAnimeData(updatedAnimeData);

    try {
      const response = await axios.post("/vote", { animeName: anime.name });
      setSnackbarMessage(`Vote recorded for ${anime.name}`);
    } catch (error) {
      const errorMessage =
        error.response && error.response.data
          ? error.response.data.error
          : "Error voting for anime";
      setSnackbarMessage(errorMessage);

      if (error.response && error.response.status === 400) {
        const originalAnimeData = animeData.map((a) =>
          a.id === anime.id ? { ...a, votes: a.votes - 1 } : a
        );
        setAnimeData(originalAnimeData);
      }
    }

    setSnackbarOpen(true);
  };

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  return (
    <div className="App">
      <header className="App-header">
        <Typography variant="h3" component="h1" color="white">
          Anime Voting System
        </Typography>
        <Typography variant="h6" component="p" color="white">
          Click on a card to vote for your favorite anime!
        </Typography>
      </header>
      <Container>
        {loading ? (
          <Box
            display="flex"
            justifyContent="center"
            alignItems="center"
            height="100vh"
          >
            <CircularProgress />
          </Box>
        ) : (
          <Grid container spacing={3}>
            {animeData.map((anime) => (
              <Grid item xs={12} sm={6} md={4} lg={3} key={anime.id}>
                <Tooltip title="Click to vote" placement="top">
                  {" "}
                  <Card
                    sx={{
                      cursor: "pointer",
                      backgroundColor: "#2c2c2c",
                      transition: "transform 0.3s",
                      "&:hover": { transform: "scale(1.05)" },
                    }}
                    onClick={() => handleCardClick(anime)}
                  >
                    <CardMedia
                      component="img"
                      height="200"
                      image={anime.image}
                      alt={anime.name}
                    />
                    <CardContent>
                      <Box
                        display="flex"
                        alignItems="center"
                        justifyContent="space-between"
                      >
                        <Typography variant="h6" component="div" color="white">
                          {anime.name}
                        </Typography>
                        <ThumbUp
                          sx={{ color: "white" }}
                          titleAccess="Click to vote"
                        />{" "}
                      </Box>
                      <Typography variant="body2" color="white">
                        Votes: {anime.votes}
                      </Typography>
                    </CardContent>
                  </Card>
                </Tooltip>
              </Grid>
            ))}
          </Grid>
        )}
      </Container>
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={4000}
        onClose={handleSnackbarClose}
      >
        <Alert onClose={handleSnackbarClose} severity="info">
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </div>
  );
};

export default App;
